
import { useState } from 'react';
import './Item.css';
const Item = ({itemId,itemName,itemDescription,itemImage,number}) => {
    const className="box box-"+number;
    const [height,setHeight] = useState("300");
    const [width,setWidth] = useState("300");
    const onImgLoad=({target:img})=>{
        setHeight(img.offsetHeight)
        setWidth(img.offsetWidth)
    }
    return ( <div className={className} style={
        {
            border:' 1px solid lightgray',
            padding: '24px',
            borderStyle: 'dotted',
            borderRadius: '12px',
            width: '45%',
            margin: '15px',
        }
        }>
        <h1>{itemName}</h1>
        <p>{itemDescription}</p>
        <img onLoad={onImgLoad} src={itemImage} alt={itemName} width={width} height={height}/>
        <h6>{itemId}</h6>
    </div> );
}
 
export default Item;