import { useHistory } from "react-router";
import './About.css';

const About = () => {
    const history = useHistory();
    const handleBack= ()=>{
        history.goBack()
    }
    return ( 
        <div className="container-fluid" >
            <div style={{backgroundColor:'white',height:'100px',padding:'10px'}}>
                <span className='backBtn' onClick={handleBack} style={{color:"blue"}}>Back</span>
                <h4>About</h4>
            </div>
        </div>
     );
}
 
export default About;