import { useEffect, useState } from "react";
import { useHistory } from "react-router";
import Item from "./Item";

const ItemsList = () => {
    const [items ,setItems] = useState([]);
    useEffect(()=>{
        fetchItems(); 
    },[])
    const history = useHistory();
    const handleBack= ()=>{
        history.goBack()
    }
    const fetchItems= async()=>{
        const data =await fetch('https://fortnite-api.theapinetwork.com/upcoming/get');
        const itemsData = await data.json();
        setItems(itemsData.data)
        console.log(itemsData.data);
    }
    return ( 
        <div className="container">
            <div style={{backgroundColor:'lightgray',height:'100px',padding:'10px'}}>
                    <span className='backBtn' onClick={handleBack} style={{color:"blue"}}>Back</span>
                    <h4>Items List</h4>
            </div>
            <div className="image-details" style={{display: 'flex',
            flexWrap: 'wrap'}}>
                {
                    items.map((item,index)=>(
                        <Item 
                            itemId={item.itemId} 
                            itemName={item.item.name} 
                            itemDescription={item.item.description}
                            itemImage={item.item.images.background}
                            itemRatings={item.item.ratings}
                            number={index}
                            key={item.itemId}
                        />
                    ))
                }
            </div>
        </div>
     );
}
 
export default ItemsList;