
import { useState } from 'react';
const Recipe = ({recipeLabel,recipeSource,recipeImage,recipeIngredients}) => {
    const [height,setHeight] = useState("300");
    const [width,setWidth] = useState("300");
    const onImgLoad=({target:img})=>{
        setHeight(img.offsetHeight)
        setWidth(img.offsetWidth)
    }
    return (
        <div style={{padding:'10px',border:'1px solid lightgray',borderRadius:'12px',marginBottom:'10px'}}>
            <div style={{padding:'15px'}}>
                <h2>{recipeLabel}</h2>
                <p>Source-{recipeSource}</p>
                <h5>Ingredients-{recipeIngredients.length}</h5>
                <img 
                    onLoad={onImgLoad}
                    src={recipeImage} 
                    alt={recipeLabel} 
                    width={width} 
                    height={height}
                    style={{borderRadius:'12px'}}
                />
            </div>
        </div> 
    );
}
 
export default Recipe;