import { useEffect, useState } from "react";
import { APP_ID, APP_KEY, RECIPE_URL } from "../utils/constants";
import Recipe from "./Recipe";

const RecipeList = () => {
    useEffect(()=>{
        fetchReceipes();
    },[])

    const [recipes,setRecipes] = useState([]);
    const [search,setSearch] = useState('');

    const fetchReceipes= async()=>{

        const searchValue=(search==='')?'chiken':search;
        const uri=`${RECIPE_URL}/search?q=${searchValue}&app_id=${APP_ID}&app_key=${APP_KEY}`;
        const data= await fetch(uri);
        const recipesLIst=await data.json();
        setRecipes(recipesLIst.hits);
        console.log(recipesLIst.hits);

    }

    const fetchSearchValues=(e)=>{
        e.preventDefault();
        fetchReceipes();
    }

    return ( 
        <div className="container">
            <form className="form row" style={{margin:"10px"}} onSubmit={fetchSearchValues}>
                <h4 className="col-md-2">Recipes</h4>
                <input 
                    type="text" 
                    className="form-control col-md-8"
                    placeholder="Search Receipes"
                    value={search} 
                    onChange={(e)=>{setSearch(e.target.value)}}
                />
                <button type="submit" className="col-md-1 ml-1 btn btn-primary">
                    Search
                </button>
            </form>

            {recipes.map((item,index)=>(
                <Recipe 
                    key={item.recipe.label + index} 
                    recipeLabel={item.recipe.label} 
                    recipeSource={item.recipe.source}
                    recipeImage={item.recipe.image}
                    recipeIngredients={item.recipe.ingredients}
                />  
            ))}
        </div>
     );
}
 
export default RecipeList;